@import "reset";
@import "vars";
@import "mixins";
.wrapper {
    width: 100%;
    position: relative;
    margin: 0 auto;
    min-height: 100vh;
    font-family: $font-primary;
    overflow-x: hidden;
    background-color: #C2EAF3;
    // @include rfs(16); // Responsive Font-Size
}

body {
    margin: 0;
}

.wrapper .background {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

.leaderboard {
    background-color: #00953a;
    border-radius: 27px 27px 35px 35px;
    position: absolute;
    right: 2.4%;
    top: 52px;
    width: 257px;
    overflow: hidden;
    img {
        height: auto;
        width: 100%;
    }
}

@media (max-width: 480px) {
    .wrapper .background {
        display: none
    }
    .leaderboard {
        margin: 0 auto;
        left: 0;
        right: 0;
        margin-top: 60px;
    }
}

.logo-outer span.first-com {
    position: absolute;
    top: 11px;
    margin-left: 9px;
}

.logo-outer span.second-com {
    position: absolute;
    top: 13px;
    margin-left: 16px;
}

.logo-outer span.three-com {
    position: absolute;
    top: 13px;
    margin-left: 16px;
}

.board-outer.second,
.board-outer.third {
    padding-top: 10px;
}

.board-outer {
    border-top: 1px solid;
    line-height: 0;
}

.leaderboard>.board-outer:first-child {
    border-top: medium none;
}

.board-inner {
    display: inline-block;
    padding: 10px 20px 25px;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

.board-outer.active .board-inner {
    padding: 31px 20px;
}

.board-outer .board-inner .logo {
    width: 37px;
    height: 37px;
    float: left;
}

.board-outer.active .board-inner .logo {
    width: 15%;
    float: left;
}

.board-outer .board-inner span {
    font-size: 20px;
    color: #FFDD00;
}

.board-outer.second .board-inner span {
    color: #E2E2E2;
}

.board-outer.third .board-inner span {
    color: #E27037;
}

.company {
    padding: 15px 20px;
    width: 100%;
    box-sizing: border-box;
}

.company .logo {
    width: 37px;
    height: 37px;
    display: inline-block;
    box-sizing: border-box;
}

.company .company-content {
    width: 81%;
    float: right;
    padding-left: 13px;
    box-sizing: border-box;
}

.board-outer.active .company {
    background-color: #50BC7C;
}

.company .company-content h2 {
    color: #ffffff;
    font-size: 10pt;
    font-weight: bold;
    margin: 0 0 7px;
    line-height: 1.1;
}

.company .company-content span {
    color: #fff;
    font-size: 10px;
}

.progress {
    margin-bottom: 9px;
    height: 4px;
    background-color: #000;
}

.progress-bar {
    background-color: #FFDD00;
    height: 100%;
}

.logo-outer {
    display: inline-block;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
}

.viewmoreimage {
    width: 27px;
    height: 30px;
    float: right;
}

.view-more {
    text-align: right;
    padding: 15px 20px;
}

.view-more span {
    color: rgba(255, 255, 255, 0.5);
    font-size: 15px;
    font-weight: 700;
    padding-right: 10px;
}

.read_more {
    cursor: pointer;
}

.board-outer>img {
    border-radius: 12px;
}

.board-outer.active {
    border: none;
}

// cloud animation
@keyframes cloud_one {
    0% {
        transform: translateX(-200px);
    }
    100% {
        transform: translateX(1600px);
    }
}

@keyframes cloud_two {
    0% {
        transform: translateX(0px);
    }
    62% {
        transform: translateX(1200px);
    }
    62.1% {
        transform: translateX(-100px);
    }
    100% {
        transform: translateX(600px);
    }
}

@keyframes cloud_three {
    0% {
        transform: translateX(-700px);
    }
    100% {
        transform: translateX(1150px);
    }
}

@keyframes cloud_four {
    0% {
        transform: translateX(0px);
    }
    22.5% {
        transform: translateX(400px);
    }
    22.6% {
        transform: translateX(-1200px);
    }
    100% {
        transform: translateX(1300px);
    }
}

#cloud-1 {
    -webkit-animation: cloud_one 60s linear infinite;
    -moz-animation: cloud_one 60s linear infinite;
    animation: cloud_one 60s linear infinite;
}

#cloud-2 {
    -webkit-animation: cloud_two 80s linear infinite;
    -moz-animation: cloud_two 80s linear infinite;
    animation: cloud_two 80s linear infinite;
}

#cloud-3 {
    -webkit-animation: cloud_three 80s linear infinite;
    -moz-animation: cloud_three 80s linear infinite;
    animation: cloud_three 80s linear infinite;
}

#cloud-4 {
    -webkit-animation: cloud_four 80s linear infinite;
    -moz-animation: cloud_four 80s linear infinite;
    animation: cloud_four 80s linear infinite;
}

// car animation
#car-1 {
    -webkit-animation: car_one 8s linear infinite;
    -moz-animation: car_one 8s linear infinite;
    animation: car_one 8s linear infinite;
}

#car-2 {
    -webkit-animation: car_two 10s linear infinite;
    -moz-animation: car_two 10s linear infinite;
    animation: car_two 10s linear infinite;
}

@keyframes car_one {
    0% {
        transform: translateX(200px);
    }
    100% {
        transform: translateX(-1600px);
    }
}

@keyframes car_two {
    0% {
        transform: translateX(200px);
    }
    100% {
        transform: translateX(-1800px);
    }
}

// bigcar animation
#bigcar-1 {
    -webkit-animation: bigcar_one 12s linear infinite;
    -moz-animation: bigcar_one 12s linear infinite;
    animation: bigcar_one 12s linear infinite;
}

#bigcar-2 {
    -webkit-animation: bigcar_two 10s linear infinite;
    -moz-animation: bigcar_two 10s linear infinite;
    animation: bigcar_two 10s linear infinite;
}

@keyframes bigcar_one {
    0% {
        transform: translateX(-200px);
    }
    100% {
        transform: translateX(1400px);
    }
}

@keyframes bigcar_two {
    0% {
        transform: translateX(-200px);
    }
    100% {
        transform: translateX(1600px);
    }
}

